@import "src/assets/colors";
@import "src/assets/mixins";

.leftContainer {
  display: flex;
  flex-direction: column;
}

.iconsWrapper {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 40px;
}

.svgFill {
  display: flex;
  gap: 23px;
  flex-direction: row;

  @include svgFill;

  & > p {
    transition: all 0.3s;
    color: $white;
  }
}

.themeLight > p {
  color: $secondary;
}